.header {
    display: flex;
    padding: 14px;
    font-size: 18px;
    font-weight: 200;
    justify-content: center;
    list-style-type: none;
    position: fixed;
    top: 0%;
    background: black;
    width: 100%;
    z-index: 1;
}
.header section {
    display: flex;
    justify-content: center;
    list-style: none;
    position: relative;
}

.navlist {
    color: white;
    text-decoration: none;
    font-size: 18px;
    margin: 0 25px;
}

.cart-number {
    position: absolute;
    color: #36c3f1;
    font-size: 18px;
    right: 1%;
    top: 0px;
    cursor: pointer;
}
