.input {
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
}

.input label {
    font-weight: bold;
    margin-right: 15px;
}

.input input {
    display: flex;
    width: 2.3rem;
    height: 2rem;
    border: 1px solid #36c3f1;
    border-radius: 3px;
    text-align: center;
}
